.brafteditor .brafteditorInner {
  border-radius: 8px;
  border: 1px solid #ddd;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 12px;
}
.brafteditor .showfullscreen {
  cursor: pointer;
}
.brafteditor .braft-output-content {
  padding: 0 10px;
}
.outputContent .ant-modal-header {
  border-radius: 0;
}
.outputContent .ant-modal {
  padding: 0;
  height: 100%;
}
.outputContent .ant-modal-content {
  height: 100%;
}
